const fs = require('fs');
const path = require('path');
const appRoot = require('app-root-path');
const { merge } = require('lodash');
const { internalLog } = require('./console');

let options = {
  /* prettyjson-256 options */
  colors:     {
    keys:    { fg:  [0, 2, 1] },
    number:  { fg: { grayscale: 11 } }
  },
  customColors : {
    bold:                     { grayscale: 23 },
    blue:                     { fg: [0, 0, 4] },
    lightBlue:                { fg: [0, 1, 5] },
    darkBlue:                 { fg: [0, 0, 3] },
    red:                      { fg: [3, 0, 0] },
    lightRed:                 { fg: [5, 0, 0] },
    darkRed:                  { fg: [1, 0, 0] },
    green:                    { fg: [0, 3, 0] },
    lightGreen:               { fg: [0, 5, 0] },
    darkGreen:                { fg: [0, 1, 0] },
    purple:                   { fg: [1, 0, 3] },
    lightPurple:              { fg: [2, 0, 5] },
    darkPurple:               { fg: [1, 0, 1] },
    cyan:                     { fg: [2, 3, 4] },
    lightCyan:                { fg: [2, 5, 5] },
    darkCyan:                 { fg: [1, 2, 3] },
    yellow:                   { fg: [4, 5, 0] },
    orange:                   { fg: [5, 2, 0] },
    white:                    { fg: [0, 3, 0] },
    gray:                     { grayscale: 12 },

    fatal:                    { fg: [5, 0, 0] },
    error:                    { fg: [4, 0, 0] },
    warn:                     { fg: [4, 2, 2] },
    log:                      { fg: [0, 2, 4] },
    info:                     { fg: [2, 2, 4] },
    debug:                    { fg: [3, 3, 4] },
    trace:                    { fg: [3, 4, 4] }
  },
  /* debugger-256 options */
  colorTag: 'color'
};
export let getOptions = () => options;

export let subsystems = [];
export const getSubsystems = () => subsystems;

let conf = false;
export const getConf = () => conf;

const fileChange = (fileName) => {
  internalLog(`Detected file change in: ${fileName}, reinitializing debugger-256`);
  conf = loadConfFile();
};

const readConfFile = (loc) => {
  const content = fs.readFileSync(loc);
  try {
    conf = JSON.parse(content);
    return conf;
  } catch(e) {
    internalLog('Error parsing configuration file', e);
    return false;
  }
};

const checkConfLocation = (loc) => {
  if (fs.existsSync(loc)) {
    !conf && internalLog(`Found debugger-256 configuration file: ${loc}, applying settings and watching for changes`);
    fs.watchFile(loc, { interval: 500 },  fileChange.bind(undefined, loc));
    return true;
  }
  return false;
};

export const loadConfFile = () => {
  // TODO: implement error handling
  const currLocation = path.resolve(__dirname, '.debugger-256');
  const rootLocation = path.resolve(appRoot.toString(), '.debugger-256');
  if (checkConfLocation(currLocation)) {
    return readConfFile(currLocation);
  } else if (checkConfLocation(rootLocation)) {
    return readConfFile(rootLocation);
  }
  return false;
};

export const initSettings = (customOptions) => {
  if (customOptions) {
    merge(options, customOptions);
  } else {
    conf = exports.loadConfFile();
    if (conf && conf['_debugger-256']) {
      merge(options, conf['_debugger-256']);
    }
  }
  return options;
};

initSettings();
